import { Component, OnInit } from '@angular/core';
import { LoginService } from '../login.service'

class LoginInfo {
	userName: 	string;
	userPwd:	string;
	constructor() { 
		this.userName = '';
		this.userPwd = '';
	}
}

@Component({
  providers: [LoginService],
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
	data: LoginInfo;
	response;
	error;
	constructor(private LoginService: LoginService) { 
		this.data = new LoginInfo();
	}
	getLoginData(user:string, pwd:string){
		this.data.userName = user;
		this.data.userPwd = pwd;
		this.LoginService.sendData(this.data.userName, this.data.userPwd).subscribe(res => this.response = res,error => this.error = <any>error);
	}
	ngOnInit() {}
}



